// ---------------------------------------------------------------
//  gird
//
// > usage
// > @include grid(width, column, gutter);
//
// ---------------------------------------------------------------
$tiny: 543px !default; // or 'em' if you prefer, of course
$small: 767px !default;
$medium: 991px !default;
$large: 1199px !default;
$extra-large: 1439px !default;
@mixin grid($number, $gutter) {
	display: flex;
	flex-wrap: wrap;
	margin-left: -$gutter / 2;
	margin-right: -$gutter / 2;
	&__col {
		box-sizing: border-box;
		margin-left: $gutter / 2;
		margin-right: $gutter / 2;
		margin-bottom: $gutter;
		width: 100%;
		min-width: 0;
		@media (min-width: $tiny + 1) {
			@if $number > 2 {
				width: calc(100% / 2 - #{$gutter});
			}
		}
		@media (min-width: $small + 1) {
			width: calc(100% / #{$number} - #{$gutter});
		}
	}
}