// ---------------------------------------------------------------
//  Base-Size
// ---------------------------------------------------------------
$base-size: 5;
$body-min-width: 1300px; //画面幅の最小値 ない場合はinitial
$wrap-width: 1000px; //wrapの幅 ない場合はauto
$wrap-max-width: initial; //最大幅 ない場合はinitial
$breakpoint-sp: 0; //SP画面へのBP ない場合は0
$breakpoint-pc: 0; //PC画面へのBP ない場合は0
$wrap-wp-margin: auto; //SP画面wrapの横余白 ない場合はauto
$gutter-pc: 25px; // PC画面のガッター
$gutter-sp: 20px; // SP画面のガッター

// ---------------------------------------------------------------
//  Font
// ---------------------------------------------------------------
$font-family-base: 'Noto Sans CJK JP', 'Noto Sans JP', sans-serif;
$font-size-base: 16;
$font-weight-base: 500;

// ---------------------------------------------------------------
//  Line-hieght
// ---------------------------------------------------------------
$line-height-base: 1.75;

// ---------------------------------------------------------------
//  color-base
// ---------------------------------------------------------------
$color-text01: #111111;
$color-text02: #333333;
$color-text03: #666666;

$color-main: #23ac38;
$color-sub: #f39800;
$color-accent: #494440;

// $color-primary
$color-primary: $color-main;
$color-primary-saturate: saturate($color-primary, 15%);
$color-primary-desaturate: desaturate($color-primary, 15%);
$color-primary-darken: darken($color-primary, 10%);
$color-primary-lighten-10: lighten($color-primary, 10%);
$color-primary-lighten-20: lighten($color-primary, 20%);
$color-primary-lighten-30: lighten($color-primary, 30%);
// $color-secondary
$color-secondary: $color-sub;
$color-secondary-saturate: saturate($color-secondary, 15%);
$color-secondary-desaturate: desaturate($color-secondary, 15%);
$color-secondary-darken: darken($color-secondary, 10%);
$color-secondary-lighten-10: lighten($color-secondary, 10%);
$color-secondary-lighten-20: lighten($color-secondary, 20%);
$color-secondary-lighten-30: lighten($color-secondary, 30%);
// $color-tertiary
$color-tertiary: $color-accent;
$color-tertiary-saturate: saturate($color-tertiary, 15%);
$color-tertiary-desaturate: desaturate($color-tertiary, 15%);
$color-tertiary-darken: darken($color-tertiary, 10%);
$color-tertiary-lighten-10: lighten($color-tertiary, 10%);
$color-tertiary-lighten-20: lighten($color-tertiary, 20%);
$color-tertiary-lighten-30: lighten($color-tertiary, 30%);
// $color-quaternary
$color-quaternary: #4e88c4;
$color-quaternary-saturate: saturate($color-quaternary, 15%);
$color-quaternary-desaturate: desaturate($color-quaternary, 15%);
$color-quaternary-darken: darken($color-quaternary, 10%);
$color-quaternary-lighten-10: lighten($color-quaternary, 10%);
$color-quaternary-lighten-20: lighten($color-quaternary, 20%);
$color-quaternary-lighten-30: lighten($color-quaternary, 30%);
// $color-border
$color-border: #6f5421;
$color-border-saturate: saturate($color-border, 15%);
$color-border-desaturate: desaturate($color-border, 15%);
$color-border-darken: darken($color-border, 10%);
$color-border-lighten-10: lighten($color-border, 10%);
$color-border-lighten-20: lighten($color-border, 20%);
$color-border-lighten-30: lighten($color-border, 30%);
// red
$color-red: #f00;
$color-red-saturate: saturate($color-red, 15%);
$color-red-desaturate: desaturate($color-red, 15%);
$color-red-darken: darken($color-red, 10%);
$color-red-lighten-10: lighten($color-red, 10%);
$color-red-lighten-20: lighten($color-red, 20%);
$color-red-lighten-30: lighten($color-red, 30%);

// ---------------------------------------------------------------
//  Margin
// ---------------------------------------------------------------
$margin-base: $base-size * 1px;
