@charset "UTF-8";

/*
* FLOCSSベース 独自拡張
* https://github.com/hiloki/flocss
* core mixin/function/variable
* fundation normalize/reset/font/iconfont/
* layout header/footer/wrapper/main
* object component/project/utility
* page
* vendor
*/

// ---------------------------------------------------------------
// core - variable
// ---------------------------------------------------------------
@import "core/variables";

// ---------------------------------------------------------------
// core - function
// ---------------------------------------------------------------


// ---------------------------------------------------------------
// core - mixin
// ---------------------------------------------------------------
@import "core/mixins/_arrow.scss";
@import "core/mixins/_button-3d-color.scss";
@import "core/mixins/_button-border-color.scss";
@import "core/mixins/_button-flat-color.scss";
@import "core/mixins/_button-ghost-color.scss";
@import "core/mixins/_button-size.scss";
@import "core/mixins/_circle.scss";
@import "core/mixins/_clearfix.scss";
@import "core/mixins/_font-line.scss";
@import "core/mixins/_grid.scss";
@import "core/mixins/_line-clamp.scss";
@import "core/mixins/_link-color.scss";
@import "core/mixins/_media.scss";
@import "core/mixins/_middle-line.scss";
@import "core/mixins/_opacity.scss";
@import "core/mixins/_position.scss";
@import "core/mixins/_rounded.scss";

.page-voice {
  .voiceCard {
    border: none;
    margin-bottom: 50px;
    display: flex;
    justify-content: space-between;
    text-decoration: none;
    color: $color-text01;
    &__photo {
      flex: 0 0 300px;
      height: 225px;
      img {
        height:100%;
        width: 100%;
        object-fit: contain;
        font-family: 'object-fit: contain;';
      }
    }
    &__body {
      flex: 0 0 380px;
      overflow: hidden;
      position: relative;
    }
    &__heading {
      @include font-line(20, 28);
      font-weight: 700;
      margin: 0 0;
    }
    &__text{
      @include font-line(16,28);
      margin: 15px 0;
      p {
        margin: 0;
      }
    }
    &__button {
      position: absolute;
      width: 150px;
      bottom: 0;
      right: 0;
      .voiceButton {
        border: 1px solid #322e2c;
        color: $color-text01;
        &:hover {
          @include opacity(1);
          background: #322e2c;
          border-color: #322e2c;
          color: #fff;
        }
      }
    }
  }

  /* detail */
  .voice-inner {
    .return {
      margin: 0 0 0 auto;
      width: 150px;
      .backButton {
        border: 1px solid #322e2c;
        color: $color-text01;
        &:hover {
          @include opacity(1);
          background: #322e2c;
          border-color: #322e2c;
          color: #fff;
        }
      }
    }
  }
  #detail {
    a {
      border: none;
    }
    .detailheading {
      margin: 0;
      @include font-line(30, 45);
      font-weight: 700;
    }
    .topBlock {
      @include clearfix();
      .zoom {
        .fa {
          color: $color-primary;
        }
      }
      &__photo {
        width: 700px;
        padding: 0;
        margin: 0 0 16px;
        a {
          display: block;
          position: relative;
          text-align: center;
          .zoomIcon {
            @include font-line(20, 20);
            display: none;
            color: #fff;
            height: 20px;
            width: 20px;
            position: absolute;
            top: 0;
            bottom: 0;
            left: 0;
            right: 0;
            margin: auto;
            z-index: 2;
          }
        }
        &:hover {
          a {
            &::before {
              background: rgba(0,0,0,0.8);
              content: "";
              position: absolute;
              top: 0;
              bottom: 0;
              right: 0;
              left: 0;
              z-index: 1;
            }
            .zoomIcon {
              display: block;
            }
          }
        }
        img {
          max-width: 100%;
          max-height: 100%;
          vertical-align: middle;
        }
        p {
          margin: 7px 0;
        }
      }
      &__memo {
        @include font-line(12);
        text-align: right;
      }
      &__content {
        p {
          margin: 20px 0 40px;
        }
      }
      &__customer,
      &__person {
        p {
          margin: 10px 0 20px;
        }
      }
    }
    .photoList {
      @include grid(3, 20px);
      padding: 0;
      width: 720px;
      &__item {
        position: relative;
        .photo {
          position: relative;
          padding-bottom: 100%;
          width: 100%;
          overflow: hidden;
          &:hover {
            .zoom {
              display: flex;
            }
          }
        }
        img {
          max-width: 100%;
          max-height: 100%;
          position: absolute;
          top: 0;
          left: 0;
          right: 0;
          bottom: 0;
          margin: auto;
        }
        .zoom {
          background-color: rgba(0, 0, 0, .7);
          width: 100%;
          height: 100%;
          display: none;
          position: absolute;
          top: 0;
          left: 0;
          i {
            width: 30px;
            height: 30px;
            margin: auto;
            @include font-line(20);
            color: #fff;
            display: flex;
            justify-content: center;
            align-items: center;
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            bottom: 0;
          }
        }
        p {
          @include font-line(11);
          text-align: center;
          margin: 7px 0;
        }
      }
    }
  }
}
